/* Write your CSS here */
.bg-container {
  background-color: white;
  height: 100vh;
  background-size: cover;
  text-align: center;
}

.container {
  background-image: linear-gradient(to bottom, #ff6e7f, #bfe9ff);
  margin: 50px;
}

.heading {
  color: #0f172a;
  font-family: 'Roboto';
}

.button {
  color: #1e293b;
  background-color: #ffffff;
  border-width: 3px;
  border-color: #0f172a;
}

.text {
  color: #334155;
  font-family: 'Roboto';
}
